import {ImageData} from "./types";

export const fetchImagePromise = (url:string) : Promise<ImageData> => {
  return new Promise((resolve, reject) => {
    const img = new Image();
    img.onabort = reject;
    img.onerror = reject;
    img.onload = () => {
      resolve({
        width: img.width,
        height: img.height,
        src: url,
      });
    };
    img.src = url;
  })
}
