import useSWR from "swr";
import React, {FC, useEffect, useMemo, useState} from "react";
import {Box, Button, CircularProgress, Paper} from "@mui/material";
import {ImageCard} from "./ImageCard";
import {ApiImage, ApiRequest} from "../../common/types";
import './ImageViewer.css';
import {useWindowSize} from "../../common/useWindowSize";

const fetchImages = (url: string) => fetch(url).then((res) => res.json());

const scrollToImage = (index: number) => document
  .querySelector(`.scroll-target-${index}`)
  ?.scrollIntoView({inline: 'center'})

export const ImageViewer: FC<{}> = () => {
  const {data} = useSWR<ApiRequest<ApiImage>>('https://cully-api.herokuapp.com/images', fetchImages);
  const {width} = useWindowSize();
  const [currImgIndex, setCurrImgIndex] = useState(0);
  const [currImgName, setCurrImgName] = useState("");

  const shouldShowCards = data && width;
  const images = data?.data || [];

  // on index change, we scroll to image
  useEffect(() => {
    if (images && images.length) {
      scrollToImage(currImgIndex)
      setCurrImgName(images[currImgIndex].filename ?? "");
    }
  }, [currImgIndex, images])

  // initialize first filename when its loaded
  useEffect(() => {
    if (images && images.length && !currImgName) {
      setCurrImgName(images[0].filename ?? "");
    }
  }, [images, currImgName])



  const imageNodes = useMemo(() => {
    return images.map((apiImage, index) =>
      <ImageCard key={`${apiImage.id}${width}`} // custom key to force rerender when window resizes
                 src={apiImage.url}
                 imageId={apiImage.id}
                 className={`scroll-target-${index}`}
      />
    )
  }, [data, width, images])

  if (shouldShowCards) {
    const images = data.data;
    const totalWidth = `${110 * images.length}vw`;
    return <>
      <div style={{width: totalWidth}} className={'ImageViewer'}>
        {imageNodes}
        <Paper className={'DockedNav'}>
          <Box sx={{
            display: 'flex',
            justifyContent: 'flex-start',
          }} p={0} m={0}>
            <Button sx={{flexGrow:1}} onClick={() => setCurrImgIndex((currImgIndex - 1 + images.length) % images.length)}></Button>
            <p style={{flexGrow:2, textAlign:'center'}} onClick={() => scrollToImage(currImgIndex)}>{currImgName}</p>
            <Button sx={{flexGrow:1}} onClick={() => setCurrImgIndex((currImgIndex + 1) % images.length)}></Button>
          </Box>
        </Paper>
      </div>
    </>
  } else {
    return <CircularProgress/>
  }
}


