import {CircularProgress, Paper} from "@mui/material";
import {FC, SyntheticEvent, useEffect, useState} from "react";
import './ImageCard.css';
import useSWR from "swr";
import {ApiFaces, ApiRequest} from "../../common/types";
import {fetchImagePromise} from "../../common/fetchImagePromise";
import classNames from "classnames";

interface ImageCardProps {
  src: string;
  rectNodes: ApiFaces[];
  className?: string;
}

type InjectImageDataProps = {
  imageId: string;
} & Omit<ImageCardProps, "width" | "height" | "rectNodes">

const fetchImageData = (args: InjectImageDataProps) => {
  return Promise.all([
    fetch(`https://cully-api.herokuapp.com/images/${args.imageId}/faces`).then((res) => res.json()),
    fetchImagePromise(args.src) //  dont really need this, just need it for preloading reasons,
  ])
}


export const _ImageCard: FC<ImageCardProps> = (props) => {
  const [imgWidth, setImgWidth] = useState<number>(0);
  const [imgHeight, setImgHeight] = useState<number>(0);
  const {src, rectNodes} = props;

  const shouldRenderFaces = Boolean(imgHeight && imgWidth);

  const setBounds = (e:SyntheticEvent<HTMLImageElement>) => {
    const element = e.currentTarget;
    setImgWidth(element.width || 0);
    setImgHeight(element.height || 0);
  };

  return <Paper className={classNames('ImageCard', props.className)}>
    <img
      src={src}
      onLoad={setBounds}
    />
    {
      shouldRenderFaces && rectNodes.map(({id, xmax, ymax, xmin, ymin}) =>
        <div key={id} className={'rect'} style={{
          left: imgWidth * xmin,
          top: imgHeight * ymin,
          width: imgWidth * (xmax - xmin),
          height: imgHeight * (ymax - ymin),
        }}></div>
      )
    }
  </Paper>
}

export const InjectedImageCard = (props: InjectImageDataProps) => {
  const {data} = useSWR(props, fetchImageData);

  if (data) {
    const [facesRequest] = data as [ApiRequest<ApiFaces>, never];
    const forwardProps = {
      ...props,
      rectNodes: facesRequest.data,
    } as ImageCardProps;
    return <_ImageCard  {...forwardProps} />
  } else {
    return <CircularProgress/>
  }
}

export const ImageCard = InjectedImageCard;
